@font-face {
    font-family: 'Montserrat';
    src: url('../../public/font/Montserrat-Regular.eot');
    src: url('../../public/font/Montserrat-Regular.eot?#iefix') format('embedded-opentype'),
        url('../../public/font/Montserrat-Regular.woff2') format('woff2'),
        url('../../public/font/Montserrat-Regular.woff') format('woff'),
        url('../../public/font/Montserrat-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Montserrat';
